import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import Onboard from "./Onboard";
import config from "./config";
import { authenticate } from "./authenticate";

import { FluentCustomizations } from "@uifabric/fluent-theme";
import { Customizer, mergeStyles, Fabric } from "office-ui-fabric-react";
import { loadTheme } from "@fluentui/react";
import { EVENT, who, trace } from "./helpers/trace";

import { Router, Link } from "@reach/router";
var x = loadTheme({
  defaultFontStyle: {
    fontFamily: "Monaco, Menlo, Consolas",
    fontWeight: "regular",
  },
  fonts: {
    small: {
      fontSize: "11px",
    },
    medium: {
      fontSize: "13px",
    },
    large: {
      fontSize: "20px",
      fontWeight: "semibold",
    },
    xLarge: {
      fontSize: "22px",
      fontWeight: "semibold",
    },
  },
});

// // Inject some global styles
// mergeStyles({
//   selectors: {
//     ':global(body), :global(html), :global(#root)': {
//       margin: 0,
//       padding: 0,
//       height: '100vh'
//     }
//   }
// });
async function start() {
  var auth = await authenticate(config, () => {
    // this is where you can send a signal to a host that intercati ons is required
  }).catch((error) => {
    console.log(error);
    document.getElementById("root").innerHTML = error;
    return;
  });
  if (auth && auth.user) {
    who(auth.user.userPrincipalName);
    const key = "HASLANDED";

    var landed = sessionStorage.getItem(key);
    if (!landed) {
      trace(EVENT.LANDING);
      sessionStorage.setItem(key, "1");
    }
  }
  // const key = "ONBOARDING";

  // var landed = sessionStorage.getItem(key);
  // if (!landed) {
  //   trace(EVENT.ONBOARDING);
  //   sessionStorage.setItem(key, "1");
  // }

  ReactDOM.render(
    <Customizer {...FluentCustomizations}>
      <Fabric theme={x}>
        <Router>
          <Onboard path="check" />
          <App auth={auth} path="/" />
        </Router>
      </Fabric>
    </Customizer>,
    document.getElementById("root")
  );
}

start();
