export const useContainerDimensions = myRef => {
  const getDimensions = () => ({
    width: myRef.current.offsetWidth,
    height: myRef.current.offsetHeight
  })

  const [dimensions, setDimensions] = useState({ width: 0, height: 0 })

  useEffect(() => {
    const handleResize = () => {
      setDimensions(getDimensions())
    }

    if (myRef.current) {
      setDimensions(getDimensions())
    }

    window.addEventListener("resize", handleResize)

    return () => {
      window.removeEventListener("resize", handleResize)
    }
  }, [myRef])

  return dimensions;
};
export const ViewTeams = (props) => {
  const [viewChannels, setViewChannels] = useState(props.details);
  const [viewDates, setViewDates] = useState(false);
 


  if (props && props.memberShips) {
    var memberShips = _.cloneDeep(props.memberShips);

    if (props.tiles) {
      props.tiles.forEach((tile) => {
        //debugger
        memberShips.push({
          displayName: tile.title,
          tile,
        });
      });

      memberShips = _.sortBy(memberShips, ["displayName"]);
    }
    return (
      <div

        className="tags"
        style={{
          padding: "4px",
          margin: "4px",
          fontSize: "16px",
          display: "flex",
          flexWrap: "wrap",
          minWidth: "300px",
        }}
      >
        <Masonry
          breakpointCols={breakpointColumnsObj}
          className="my-masonry-grid"
          columnClassName="my-masonry-grid_column"
        >
          {/* array of JSX items */}

          {memberShips.map((team, key) => {
            //    if ((!team.resourceProvisioningOptions || !team.resourceProvisioningOptions.length > 0) && (team.creationOptions && !team.creationOptions.length > 0)) return null
            //if ( team["@odata.type"] === "#microsoft.graph.directoryRole") return null
            // Profoe: "https://365adm.sharepoint.com/_layouts/groupstatus.aspx?id=786dc6d7-766f-46df-ac4b-d6883597061f&target=profile"

            return (
              <ViewTeam
                key={key}
                team={team}
                viewChannels={props.details}
                viewDates={viewDates}
                onSaveTeam={props.onSaveTeam}
                {...props}
              />
            );
          })}
        </Masonry>
      </div>
    );
  } else {
    return null;
  }
};
function teamsLink(href) {
  if (href) {
    var part1 = href.split("https://teams.microsoft.com/l/team/");
    var part2 = part1[1].split("/conversations?");
    var part3 = href.split("groupId=");
    var part4 = part3[1].split("&");
    var part5 = href.split("tenantId=");
    var threadId = part2[0];
    var groupId = part4[0];
    var tenantId = part5[1];
    var conversationsHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/conversations/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    var filesHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/files/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    return conversationsHref;
  } else {
    return;
  }
}
function teamsLink2(href) {
  if (href) {
    var part1 = href.split("https://teams.microsoft.com/l/channel/");
    var part2 = part1[1].split("/conversations?");
    var part3 = href.split("groupId=");
    var part4 = part3[1].split("&");
    var part5 = href.split("tenantId=");
    var threadId = part2[0];
    var groupId = part4[0];
    var tenantId = part5[1];
    var conversationsHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/conversations/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    var filesHref = `https://teams.microsoft.com/_?tenantId=${tenantId}#/files/unknown?groupId=${groupId}&threadId=${threadId}&ctx=channel`;
    return conversationsHref;
  } else {
    return;
  }
}
